<template>
  <div class="home">
    <header>
      <div>
        <h1 class="title">ICO</h1>
        <div class="sub">
          <span>ICO</span>
          <span></span>
        </div>
      </div>
    </header>
    <section>
      <h2 class="sec-title">ICO</h2>
      <article class="index">
        <div class="left">
          <div id="myChart" style="height:400px;"></div>
          <p class="x-axis">
            <span v-for="item in xAxis">{{item}}</span>
          </p>
        </div>
        <div class="right">
          <div class="danger">
            <section>
              <span class="cur-index">:</span>
              <div class="num">{{icoScore}}</div>
              <span></span>
            </section>
            <section>
              <span class="sub-title">ICO</span>
              <span class="tips">icoico</span>
            </section>
          </div>
          <div class="data">
            <div v-for="item in allIco" class="item">
              <span class="title">{{item.title}}</span>
              <span class="number">{{item.number}}</span>
            </div>
          </div>
        </div>
      </article>
    </section>
    <section class="main_width">
      <h2 class="sec-title"></h2>
      <article class="level flex_row_4">
        <Project v-for="item,k in level" :project="item" :key="k"></Project>
      </article>
    </section>
    <section class="introduce">
      <h2 class="sec-title first">ICO</h2>
      <article class="content">
        ICOInitial Crypto-Token OfferingCrypto-Token DAO  
      </article>
      <h2 class="sec-title">ICO</h2>
      <article class="content">
        ICOInitial Coin Offering/ ico/ ico ICO ico ICO  ico
      </article>
    </section>
    <section class="advantage">
      <h2 class="sec-title"></h2>
      <article class="item-lists">
        <div v-for="item in advantage" class="item">
          <div class="bg"></div>
          <img v-bind:src="item.img">
          <aside>
            <span class="title">{{item.title}}</span>
            <span class="content">{{item.content}}</span>
          </aside>
        </div>
      </article>
    </section>
    <section class="main_width help">
      <h2 class="sec-title"></h2>
      <article class="item-lists">
        <div v-for="item in help" class="item">
          <div class="ques-mark"></div>
          <span class="question">{{item.question}}</span>
        </div>
      </article>
    </section>
    <section class="main_width partner">
      <h2 class="sec-title"></h2>
      <article class="item-lists">
        <div v-for="item in partner" class="item" @mouseenter="item.active=!item.active" @mouseleave="item.active=!item.active">
          <img v-bind:src="item.img" v-if="!item.active"/>
          <img v-bind:src="item.activeImg" v-if="item.active"/>
        </div>
      </article>
    </section>
  </div>
</template>

<script>
import api from '../api/index'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/line'
import Project from '@/components/Parts/Project'
export default {
  name: 'home',
  data () {
    return {
      icoScore: null,
      xAxis: ['2', '3', '4', '5'],
      yAxis: [5, 8, 4, 7],
      allIco: [
        { title: 'ICO', sign: 'ico_score', number: '' },
        { title: 'ICO', sign: 'ico_num', number: '' },
        { title: 'ICO()', sign: 'ico_amount', number: '' }
      ],
      level: [],
      advantage: [
        { img: require('@/assets/images/home/ad1.png'), title: 'ICO', content: 'ICO' },
        { img: require('@/assets/images/home/ad2.png'), title: 'ICO', content: 'ICOICOICO' },
        { img: require('@/assets/images/home/ad3.png'), title: 'ICO', content: 'ICO' }
      ],
      help: [
        { img: require('@/assets/images/home/help.png'), question: 'ICO' },
        { img: require('@/assets/images/home/help.png'), question: 'ICO' },
        { img: require('@/assets/images/home/help.png'), question: 'ICO' },
        { img: require('@/assets/images/home/help.png'), question: 'ICO' }
      ],
      partner: [
        { img: require('@/assets/images/home/logo/logo-aliyun-1.png'), activeImg: require('@/assets/images/home/logo/logo-aliyun.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-FACTOM-1.png'), activeImg: require('@/assets/images/home/logo/logo-FACTOM.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-fujiakeji-1.png'), activeImg: require('@/assets/images/home/logo/logo-fujiakeji.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-GOSUN-1.png'), activeImg: require('@/assets/images/home/logo/logo-GOSUN.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-shuniujinfu-1.png'), activeImg: require('@/assets/images/home/logo/logo-shuniujinfu.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-qianxinwang-1.png'), activeImg: require('@/assets/images/home/logo/logo-qianxinwang.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-shibei-1.png'), activeImg: require('@/assets/images/home/logo/logo-shibei.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-hangzhoudifangshuiwuju-1.png'), activeImg: require('@/assets/images/home/logo/logo-hangzhoudifangshuiwuju.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-xinanzaixian-1.png'), activeImg: require('@/assets/images/home/logo/logo-xinanzaixian.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-yinjianggufen-1.png'), activeImg: require('@/assets/images/home/logo/logo-yinjianggufen.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-yishanghudong-1.png'), activeImg: require('@/assets/images/home/logo/logo-yishanghudong.png'), active: false },
        { img: require('@/assets/images/home/logo/logo-zhejiangqinghuachangsanjiao-1.png'), activeImg: require('@/assets/images/home/logo/logo-zhejiangqinghuachangsanjiao.png'), active: false }
      ]
    }
  },
  beforeCreate: function () {
    api.post('home/ico_list', { sign: 'token=0' })
      .then((res) => {
        this.icoScore = res.ico_score
        for (let i = 0, len = this.allIco.length; i < len; i++) {
          let sign = this.allIco[i].sign
          this.allIco[i].number = res[sign]
        }
      }).catch(error => {
        console.log(error)
      })

    api.post('home/hot_list', { sign: 'token=0' })
      .then((res) => {
        this.level = res
        console.log(this.level)
      }).catch(error => {
        console.log(error)
      })
  },
  mounted () {
    this.drawLine(this.yAxis)
  },
  methods: {
    drawLine (data) {
      let myChart = echarts.init(document.getElementById('myChart'))
      myChart.setOption({
        title: {
          text: 'ICO'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        grid: {
          left: '-4%',
          right: '0',
          bottom: '5%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            show: false,
            data: []
          }
        ],
        yAxis: [
          {
            show: false
          }
        ],
        series: [
          {
            name: 'ICO',
            type: 'line',
            areaStyle: {

            },
            data: data,
            lineStyle: {
              normal: {
                color: '#fff'
              }
            },
            showSymbol: false,
            smooth: true
          }
        ]
      })
    }
  },
  components: {
    Project
  }
}
</script>

<style lang='scss' scoped>
@import '../assets/css/home.scss';
</style>



